.header {
  position: fixed;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  // margin-bottom: 80px;

  display: flex;
  justify-content: center;
  z-index: 1;

  background-image: linear-gradient(180deg, #23379a, #09133c);

  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  // margin-bottom: 80px;

  display: flex;
  justify-content: center;
  z-index: 1;

  background-image: linear-gradient(180deg, #23379a, #09133c);

  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.container {
  display: block;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
